@import "./variable.scss";
@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

body,h1,h2,h3,p,span,div {
    transition: all ease 0.2s 0s;
    -moz-transition: all ease 0.2s 0s;
    -webkit-transition: all ease 0.2s 0s;
}
body {
    background-color: var(--bkg-color);
}
h1,h2,h3,p,span,div {
    color: var(--text-color) !important;
}

//== LAYOUT ==//
#root,.App,#Home {
    height: 100vh;
}
body {
    min-height: 100vh;
    max-width: 100vw;
    margin: 0;
    overflow: hidden;
}
.container {
    max-width: 100% !important;
}
.row {
    height: 100%;
}
.col-md-3 {
    margin-bottom: 35px;
    height: fit-content;
}
.layout {
    width: 100%;
}
.view-layout {
    height: 100%;
    width: 100%;
    overflow: hidden;
    word-wrap: break-word;
    position: fixed;
}
.pd-12 {
    padding: 0 12px;
}

//== FONT ==//
p,a,h1,h2,h3 {
    font-size: $ft-s;
    font-weight: $f-w;
    letter-spacing: $l-s;
}
ul,div {
    font-weight: $f-w;
    letter-spacing: $l-s;
}
p {
    line-height: $l-h;
    text-align: left;
    margin-bottom: 2rem;
}
a {
    color: var(--text-color-blue);
    text-decoration: underline;
    line-height: $l-h;
    transition: all ease 0.1s 0s;
    -moz-transition: all ease 0.1s 0s;
    -webkit-transition: all ease 0.1s 0s;
}
a:hover {
    color: var(--text-color-blue);
    text-decoration: none;
    transition: all ease 0.1s 0s;
    -moz-transition: all ease 0.1s 0s;
    -webkit-transition: all ease 0.1s 0s;
}
ul {
    padding: 0;
    margin: 0;
    list-style: none;
    li {
        font-size: $ft-s-s;
        a {
            letter-spacing: $l-s;
            font-weight: $f-w;
        }
    }
}
::selection {
    color: var(--bkg-color);
	background-color: $red;
}
::-moz-selection {
    color: var(--bkg-color);
    background-color: $bl;
}
.ft-s-s {
    p,a {
        font-size: $ft-s-s;
        line-height: $l-h-s;
    }
}
.ft-s-ss {
    p,a {
        font-size: $ft-s-ss;
        line-height: $l-h-ss;
    }
}
a {
    .f-n {
        color: var(--text-color-blue) !important;
    }
}
.f-n {
    font-size: $ft-s-f-n;
    vertical-align: 4.5px;
    -webkit-text-stroke: 0.2px;
}
.f-n-s {
    font-size: 0.5rem;
    vertical-align: 6px;
    -webkit-text-stroke: 0.2px;
}
.t-underline {
    text-decoration: underline;
}

//== LINE ==//
.outLine {
    border: $b-w solid var(--text-color);
    // padding: 10px;
    padding: 8px 10px 10px;
    overflow: auto;
    max-height: 73vh;
    // margin-top: 80px;
    margin-top: 50px;
}
.bottomLine {
    display: inline;
    font-size: $ft-s;
    font-weight: bold;
    border-bottom: $b-w solid var(--text-color);
}

//== DEVICE SCREEN ==//
/*대부분의 스마트폰 모바일 기기(세로)*/
@media (min-width: 320px) and (max-width: 480px) {
    .view-layout {
        padding: 0 7px;
        overflow: auto;
        overflow-x: hidden;
        -webkit-overflow-scrolling: touch;
    }
    .outLine {
        margin-top: 43px;
    }
}

/*저해상도 태블릿, 모바일(가로)*/
@media (min-width: 481px) and (max-width: 767px) {
    .view-layout {
        padding: 0 7px;
        overflow: auto;
        overflow-x: hidden;
        -webkit-overflow-scrolling: touch;
    }
}
